.hidden {
    transition: 0.5s;
    opacity: 0;
    transform: translateY(5%);
}

.hidden.hide {
    display: none;
}

.hide {
    z-index: -1;
    opacity: 0;
    position: absolute;
    /* display: none; */
    transform: translateY(5%);
}

.show {
    display: block;
    opacity: 1;
    transition: 0.5s;
    position: relative;
    transform: translateY(-5%);
}

.disp {
    opacity: 0;
    transition: 0.5s;
    transform: translateY(5%);
}

.apps {
    opacity: 1;
    transition: 0.5s;
    transform: translateY(-5%);
}

img {
    height: 500px;
}

