.swiper {
    display: flex;
    flex-direction: column-reverse;
    width: 100%;

    @media (max-width: 767.98px) {
        width: 70%;
    }
}

.swiper-slide {
    display: flex;
    justify-content: center;
    width: 10%;
}

.swiper-slide {
    transition: 0.5s;
    opacity: 0;
    transform: translateY(5%);

    img,
    h1 {
        opacity: 0;
        transition: 0.5s;
        transform: translateY(30%);
    }
    @media (max-width: 767.98px) {
        h1 {
            font-size: 50px;
            opacity: 1;
            transition: 0.5s;
            transition-delay: 250ms;
            transform: translateY(10%);
        }
    }
}

.swiper-slide-active {
    transition-delay: 250ms;
    opacity: 1;
    transition: 0.5s;
    transform: translateY(-5%);

    img,
    h1 {
        opacity: 1;
        transition: 0.5s;
        transition-delay: 250ms;
        transform: translateY(0%);
    }

    h1 {
        display: none;
    }

    @media (min-width: 767.98px) {
        
    }

    @media (max-width: 767.98px) {
        h1 {
            font-size: 50px;
            opacity: 1;
            transition: 0.5s;
            transition-delay: 250ms;
            transform: translateY(-50%);
            display: none;
        }
    }
}

.swiper-pagination-bullets{
    // margin-top: 50%;
}

.swiper-pagination-bullet {
    height: 40px;
    width: 75px;
    font-size: 10px;
    transition: 0.25s;
    color: #fff;
    background: rgb(0, 0, 0);
}

.swiper-pagination-bullet-active {
    height: 40px;
    width: 75px;
    color: rgb(0, 0, 0);
    background: #fff;
    transition: 0.25s;
}

.swiper-pagination-bullet-active::before {
    /* content: "1"; */
}

.swiper-button-next {
    background-color: white;
    border-radius: 50px;
    padding: 30px;
    &::after {
        color: black;
    }  
}

@media (max-width: 767.98px) {
    .swiper-button-next {
       display: none;
    }
}

.swiper-button-prev {
    background-color: white;
    border-radius: 50px;
    padding: 30px;
    &::after {
        color: black;
    }
}




.activeDot {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-right: 10px;
    line-height: 0px;
    span {
        color: yellow;
        font-size: 75px;
        line-height: 0;
    }
    transition: 0.5s;
}

.inactive {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-right: 10px;
    line-height: 0px;
    span {
        font-size: 0px;
    }
    transition: 0.5s;
}

.Contact,
.About {
    @media (max-width: 767.98px) {
        margin: 25px 50px;
    }
}


.swiper-button-prev,
    .swiper-button-next {
        opacity: 1;
        display: none;

        &::after {
            display: none;
        }
    }


